<template lang="html">
  <!--  -->
  <section class="full-fixed de-comment">
    <lc-header title="" @callBack="back"></lc-header>
    <section class="main">
      <scroll :data="commentData" ref="listview">
        <!-- divscroll -->
        <div>
          <!--  -->
          <div class="de-top">
            <div class="de-top-t">
              <p class="text"></p>
              <div class="star">
                <span class="star-light"></span>
              </div>
              <p class="minute">4</p>
            </div>
          </div>
          <!--  -->
          <div class="de-body">
            <comments :comment-data="commentData"></comments>
          </div>
        </div>
      </scroll>
    </section>
  </section>
</template>

<script>
import Scroll from "@/components/Scroll";
import Comments from "@/components/Comment";
import LcHeader from '@/components/Header';

export default {
  data() {
    return {
      commentData: []
    };
  },
  created() {
    this._getListData();
  },
  methods: {
    _getListData() {
      // 

      this.commentData = [
        // 
        {
          username: "",
          userportrait: require("@/common/img/user-img.png"),
          times: "2017-10-31",
          start: "5",
          content:
            "",
          minisrc: [
            {
              src: require("@/common/img/comment-1.jpg")
            },
            {
              src: require("@/common/img/comment-1.jpg")
            },
            {
              src: require("@/common/img/comment-1.jpg")
            }
          ]
        },
        {
          username: "",
          userportrait: require("@/common/img/user-img.png"),
          times: "2017-10-31",
          start: "4",
          content:
            "",
          minisrc: [
            {
              src: require("@/common/img/comment-1.jpg")
            },
            {
              src: require("@/common/img/comment-1.jpg")
            },
            {
              src: require("@/common/img/comment-1.jpg")
            },
            {
              src: require("@/common/img/comment-1.jpg")
            }
          ]
        },
        {
          username: "",
          userportrait: require("@/common/img/user-img.png"),
          times: "2017-10-31",
          start: "3",
          content:
            "",
          minisrc: [
            {
              src: require("@/common/img/comment-1.jpg")
            },
            {
              src: require("@/common/img/comment-1.jpg")
            }
          ]
        }
      ];
    },
    back() {
      this.$router.back();
    }
  },
  components: {
    Scroll,
    Comments,
    LcHeader
  }
};
</script>

<style scoped lang="scss">
@import "../common/sass/variable";
@import "../common/sass/mixin";

.main {
  position: absolute;
  top: 1.2rem /* 45/37.5 */;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
}
.de-comment {
  z-index: 10;
  background: #fff;
}
.de-top {
  @include border-b-1px(0);
  .de-top-t {
    display: flex;
    align-items: center;
    padding: 0.27rem /* 10/37.5 */;
    .text {
      padding-right: 0.27rem /* 10/37.5 */;
      font-size: 0.37rem /* 14/37.5 */;
      color: $color-text;
    }
    .minute {
      padding-left: 0.27rem /* 10/37.5 */;
      font-size: 0.37rem /* 14/37.5 */;
      color: $color-theme;
    }
    .star {
      position: relative;
      width: 3.31rem /* 124/37.5 */;
      height: 0.59rem /* 22/37.5 */;
      background: url("../common/img/star.png") no-repeat;
      background-size: 3.31rem /* 124/37.5 */;
      .star-light {
        position: absolute;
        top: 0;
        left: 0;
        width: 80%; // 
        height: 100%;
        background: url("../common/img/star.png") no-repeat;
        background-size: 3.31rem /* 124/37.5 */;
        background-position: 0 -0.59rem /* 22/37.5 */;
      }
    }
  }
}
</style>



