<template lang="html">
  <!--  -->
  <section class="full-fixed list-detail">
    <transition name="list-header">
      <header class="max-center list-header" v-show="titleShow">
        <div class="top">
          <i class="iconfont icon-back" @click="back">
            <svg class="icon" aria-hidden="true"><use xlink:href="#icon-fanhui"></use></svg>
          </i>
          <div class="head-wrap">
            <!--  -->
            <div>
              <ul class="list-nav">
                <li class="list-li" v-for="(item, index) in listNav" @click="_scrollTo(index)" :class="{'current':currentIndex===index}" :key="index" ref="menuList">{{item}}</li>
              </ul>
            </div>
          </div>
          <!-- <i class="iconfont waves-effect waves-circle" @click="bindOpenShopping">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-gouwuche"></use>
            </svg>
          </i> -->
        </div>
      </header>
    </transition>
    <!--  -->
    <img class="swiper-img" :src="listDetail.img" style="width: 100%;" ref="scaleImg" alt="">
    <!--  -->
    <scroll
      class="recommend-content"
      :data="commentData"
      :probe-type="probeType"
      :listen-scroll="listenScroll"
      @scroll="scrollPos"
      ref="scrolls"
    >
      <!--  -->
      <section>
        <div class="basic" ref="basic">
          <!--  -->
          <div class="swiper">
            <!-- <img class="swiper-img" :src="listDetail.img" style="width: 100%;" ref="scaleImg" alt=""> -->
          </div>
          <!--  -->
          <div class="commodity-text">
            <!-- <commodity-text :title-style="titleStyle" :data="listDetail"></commodity-text> -->
            <h3 class="title">{{listDetail.title}}</h3>
            <div class="tags">
              <span class="title-tag" v-for="tag in listDetail.props">{{tag}}</span>
            </div>
            <div class="price-wrap">
              <span class="price">
                <i class="icon-renminbi"><svg class="icon" aria-hidden="true"><use xlink:href="#icon-renminbi"></use></svg></i>{{listDetail.price}}
              </span>
              <s class="orgPrice">
                <i class="icon-renminbi"><svg class="icon" aria-hidden="true"><use xlink:href="#icon-renminbi"></use></svg></i>{{listDetail.orgPrice}}
              </s>
            </div>
            <div class="food-text">
              <span class="freight"></span>
              <span class="freight"> {{listDetail.sale}}</span>
            </div>
          </div>
          <div class="hr-10"></div>
        </div>
        <!--  -->
        <div class="select" ref="topHeight" @click="_selectShow">
          <span class="select-text"> </span>
          <i class="iconfont">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-jiantou-copy-copy"></use>
            </svg>
          </i>
        </div>
        <div class="bottom">
          <!--  -->
          <div class="list-conster parameter" ref="listGroup0">
            <div class="hr-10"></div>
            <ul class="parameter-wrap">
              <li class="parameter-list bad"></li>
              <li class="parameter-list" v-for="(item, index) in parameterData" :key="index">
                <span class="parameter-title">{{item.title}}</span>
                <span class="parameter-desc">{{item.desc}}</span>
              </li>
            </ul>
          </div>
          <!--  -->
          <div class="list-conster particulars" ref="listGroup1">
            <div class="hr-10"></div>
            <img class="particulars-img" :src="item" :key="index" v-for="(item, index) in particularsData" @load="_srcollRefresh">
          </div>
          <!--  -->
          <div class="list-conster comment-border" ref="listGroup2">
            <div class="hr-10"></div>
            <comments :comment-data="commentData"></comments>
            <div class="comment-more">
              <span class="waves-effect waves-button more-btn" @click="_openPage('comment')"></span>
            </div>
          </div>
        </div>
      </section>
    </scroll>
    <!--  -->
    <section class="max-center footer-bar">
      <ul class="bar-wrap">
        <li class="bar-list" @click="_collect">
          <i class="iconfont" v-show="!iconCollect"><svg class="icon" aria-hidden="true"><use xlink:href="#icon-shoucang1"></use></svg></i>
          <i class="iconfont" v-show="iconCollect"><svg class="icon" aria-hidden="true"><use xlink:href="#icon-shoucang"></use></svg></i>
          <p class="bar-text"></p>
        </li>
        <li class="bar-list" @click="_openPageStore">
          <i class="iconfont">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-dianpu-copy"></use>
            </svg>
          </i>
          <p class="bar-text"></p>
        </li>
        <li class="bar-list" @click="_openPage('info')">
          <i class="iconfont">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-icon1"></use>
            </svg>
          </i>
          <p class="bar-text"></p>
        </li>
        <li class="bar-list-add bar-yellow">
          <p class="bar-add-text" @click="_addCart"></p>
        </li>
        <li class="bar-list-add bar-red">
          <p class="bar-add-text" @click="_addMember"></p>
        </li>
      </ul>
    </section>
    <!--  -->
    <select-size-color
      v-show="isSelectShow"
      @selectHide="selectHide"
      @addCart="selectAddCart"
      @setSelect="setSelect"
      @submit="submit"
      :specification-data="specificationData">
    </select-size-color>
    <!--  -->
    <transition name="transX">
      <router-view></router-view>
    </transition>
  </section>
</template>

<script>
import Scroll from "@/components/Scroll";
import Swiper from "@/components/Swiper";
import CommodityText from "@/components/CommodityText";
import selectSizeColor from "@/components/selectSizeColor";
import Comments from "@/components/Comment";
import { mapMutations, mapGetters } from "vuex";
import { prefixStyle } from "@/common/js/dom";

const transform = prefixStyle("transform");

export default {
  data() {
    return {
      discList: [],
      specificationData: {}, // 
      selectSpec: {}, // 
      isSelectShow: false, // 
      parameterData: [], // 
      particularsData: [], // 
      commentData: [], // 
      listNav: ["", "", ""],
      titleShow: false, // 
      listHeight: [],
      scrollY: 0,
      currentIndex: 0,
      decline: false,
      fullPath: "", // 
      id: "", //  ID 
      iconCollect: false, // 
      probeType: 3, // 
      listenScroll: true,
      autoplay: 0, // 
      titleStyle: true // 
    };
  },
  created() {
    this.init();
  },
  activated() {
    // router-view 
    this.init();
  },
  mounted() {
    this.imageHeight = this.$refs.scaleImg.clientHeight;
    this.scrolls = this.$refs.scrolls;
  },
  computed: {
    ...mapGetters(["listDetail", "swiperFull", "shoppingData"])
  },
  methods: {
    // 
    init() {
      this.titleShow = false;
      this.scrolls && this.scrolls.scrollTo(0, 0, 30);
      this.particularsData = []; // 
      this.fullPath = this.$route.fullPath.replace("/classify", ""); // 
      this.id = this.$route.params.id; //  id
      if (!this.listDetail.img && !this.id) {
        //  
        this.$router.replace({
          path: "/list"
        });
      } else {
        this._getListData()
      }
    },
    scrollPos(pos) {
      // 
      this.scaleImg(pos.y);
      this.scrollY = pos.y;
    },
    scaleImg(newVal) {
      let scale = 1;
      const percent = Math.abs(newVal / this.imageHeight).toFixed(2);
      const transY = Math.max((newVal / 2).toFixed(2), -this.imageHeight);

      if (newVal > 0) {
        scale = 1 + Number(percent);
        this.$refs.scaleImg.style[
          transform
        ] = `translate3d(0, 0, 0) scale(${scale})`;
      } else {
        this.$refs.scaleImg.style[
          transform
        ] = `translate3d(0, ${transY}px, 0) scale(1)`;
      }
    },
    _calculateHeight() {
      // 
      this.listHeight = [];
      // 
      this.listGroup = [];
      let listGroup0 = this.$refs.listGroup0;
      let listGroup1 = this.$refs.listGroup1;
      let listGroup2 = this.$refs.listGroup2;
      this.listGroup.push(listGroup0);
      this.listGroup.push(listGroup1);
      this.listGroup.push(listGroup2);
      let height = this.$refs.basic.clientHeight; // 
      this.listHeight.push(height);
      for (let i = 0; i < this.listGroup.length; i++) {
        let item = this.listGroup[i];
        height += item.clientHeight;
        this.listHeight.push(height);
      }
    },
    _scrollTo(index) {
      // 
      this.scrolls.scrollToElement(
        this.listGroup[index],
        300,
        0,
        -this.topHeight
      ); // 
    },
    _srcollRefresh() {
      // 
      if (this.loadimgTime) {
        clearTimeout(this.loadimgTime);
      }
      this.loadimgTime = setTimeout(() => {
        if (this.scrolls) {
          this.scrolls.refresh();
        }
        if (this._calculateHeight) {
          this._calculateHeight();
        }
      }, 30);
    },
    _selectShow() {
      // 
      this.isSelectShow = true;
    },
    // 
    selectHide() {
      this.isSelectShow = false;
    },
    // 
    setSelect(data) {
      this.selectSpec = data;
    },
    // 
    selectAddCart() {
      const shoppingData = this.localStorage.get("shoppingData") || [];
      const listDetail = this.listDetail;

      // 
      for (let i = 0, leng = shoppingData.length; i < leng; i++) {
        if (shoppingData[i].tradeItemId === listDetail.tradeItemId) {
          this.setAlertHint({
            lsattr: true,
            text: ""
          });
          return;
        }
      }
      let obj = Object.assign({}, this.selectSpec, listDetail);
      shoppingData.unshift(obj);    // 
      this.localStorage.set("shoppingData", shoppingData);

      this.setAlertHint({
        lsattr: true,
        text: ""
      });
    },
    _addCart() {
      // 
      let selectSpec = this.selectSpec;
      if (!selectSpec.color || !selectSpec.size) {
        this.isSelectShow = true;
      } else {
        this.selectAddCart();
      }
    },
    // -
    submit() {
      this.setListDetail(Object.assign({}, this.listDetail, this.selectSpec)); // 
      this.$router.push({
        path: `${this.fullPath}/submit`
      });
    },
    _addMember() {
      // 
      let selectSpec = this.selectSpec;
      if (!selectSpec.color || !selectSpec.size) {
        this.isSelectShow = true;
      } else {
        this.setListDetail(Object.assign({}, this.listDetail, this.selectSpec)); // 
        this.$router.push({
          path: `${this.fullPath}/submit`
        });
      }
    },
    _collect() {
      // 
      this.iconCollect = !this.iconCollect;
      if (this.iconCollect) {
        this.setAlertHint({
          lsattr: true,
          text: "",
          time: 1500
        });
      } else {
        this.setAlertHint({
          lsattr: true,
          text: "",
          time: 1500
        });
      }
    },
    _openPageStore() {
      // 
      this.$router.push({
        path: `/store`
      });
    },
    _openPage(path) {
      // 
      this.$router.push({
        path: `/list/detail/${this.id}/${path}`
      });
    },
    // 
    bindOpenShopping() {
      this.$router.push({
        path: "/shopping"
      });
    },
    _getListData() {
      // 
      this.specificationData = {
        color: ["", "", ""],
        size: ["S", "M", "L"]
      };
      this.specificationData.img = this.listDetail.img; // 
      this.specificationData.price = this.listDetail.price; // 
      this.parameterData = [
        {
          title: "",
          desc: "91.5% ()8.5%"
        },
        {
          title: "",
          desc: "()"
        },
        {
          title: "",
          desc: "317201562"
        },
        {
          title: "",
          desc: "Vero Moda"
        },
        {
          title: "",
          desc: ""
        },
        {
          title: "",
          desc: "18-38"
        },
        {
          title: "",
          desc: ""
        },
        {
          title: "",
          desc: ""
        },
        {
          title: "",
          desc: ""
        },
        {
          title: "",
          desc: "2017"
        }
      ];
      // 
      this.commentData = [
        // 
        {
          username: "",
          userportrait: require("../common/img/user-img.png"),
          times: "2017-10-31",
          start: "5",
          content:
            "",
          minisrc: [
            {
              src: require("../common/img/comment-1.jpg")
            },
            {
              src: require("../common/img/comment-1.jpg")
            },
            {
              src: require("../common/img/comment-1.jpg")
            }
          ]
        },
        {
          username: "",
          userportrait: require("../common/img/user-img.png"),
          times: "2017-10-31",
          start: "4",
          content:
            "",
          minisrc: [
            {
              src: require("../common/img/comment-1.jpg")
            },
            {
              src: require("../common/img/comment-1.jpg")
            },
            {
              src: require("../common/img/comment-1.jpg")
            },
            {
              src: require("../common/img/comment-1.jpg")
            }
          ]
        },
        {
          username: "",
          userportrait: require("../common/img/user-img.png"),
          times: "2017-10-31",
          start: "3",
          content:
            "",
          minisrc: [
            {
              src: require("../common/img/comment-1.jpg")
            },
            {
              src: require("../common/img/comment-1.jpg")
            }
          ]
        }
      ];

      this._particularsData(); // 
      setTimeout(() => {
        if (this.scrolls) {
          this.scrolls.refresh();
        }
        if (this._calculateHeight) {
          this._calculateHeight();
        }
        this.topHeight = this.$refs.topHeight.clientHeight; //  header 
      }, 20);
    },
    _particularsData() {
      // 
      let img = this.listDetail.img;
      for (let i = 0; i < 3; i++) {
        this.particularsData.push(img);
      }
    },
    // 
    back() {
      this.$router.back();
    },
    ...mapMutations({
      setListDetail: "SET_LIST_DETAIL",
      setSpecificationData: "SET_SPECIFICATION_DATA",
      setSwiperFull: "SET_SWIPER_FULL",
      setSwiperFullIndex: "SET_SWIPER_FULL_INDEX",
      setAlertHint: "SET_ALERT_HINT",
      setRouterAnim: "SET_ROUTER_ANIM"
    })
  },
  watch: {
    scrollY(newY) {
      // console.log(newY)
      let n = Math.ceil(-newY); // 
      newY = -n - 1; // + 1 
      const listHeight = this.listHeight;
      // newY>0
      if (newY > 0 || -newY < this.listHeight[0]) {
        this.currentIndex = 0;
        this.titleShow = false;
        return;
      }
      this.titleShow = true;
      // currentIndex 
      let fullHeight =
        this.listHeight[this.listHeight.length - 1] -
        this.listHeight[0] +
        this.topHeight;
      if (-newY >= fullHeight) {
        this.currentIndex = listHeight.length - 2;
        return;
      }
      // 
      for (let i = 0; i < listHeight.length - 1; i++) {
        let height1 = listHeight[i];
        let height2 = listHeight[i + 1];
        if (-newY >= height1 && -newY < height2) {
          this.currentIndex = i;
          return;
        }
      }
      // -newY
      this.currentIndex = listHeight.length - 2;
    }
  },
  components: {
    selectSizeColor,
    Scroll,
    Swiper,
    CommodityText,
    Comments
  }
};
</script>

<style scoped lang="scss">
@import "../common/sass/variable";
@import "../common/sass/mixin";

.list-detail {
  overflow: hidden;
  background-color: #fff;
}
// 
.list-header {
  position: fixed;
  top: 0;
  z-index: 9;
  width: 100%;
  height: 1.2rem /* 45/37.5 */;
  background: #fff;
  .top {
    display: flex;
    align-items: center;
    @include border-b-1px(0);
    .iconfont {
      width: 1.2rem /* 45/37.5 */;
      font-size: 0.48rem /* 18/37.5 */;
      color: $color-text;
      text-align: center;
      line-height: 1.2rem /* 45/37.5 */;
    }
    .icon-back {
      color: $color-theme;
    }
    .head-wrap {
      flex: 1;
      text-align: center;
    }
  }
  .list-nav {
    display: flex;
    padding: 0 0.53rem /* 20/37.5 */;
    line-height: 1.2rem /* 90/75 */;
    .list-li {
      flex: 1;
      font-size: 0.43rem /* 16/37.5 */;
      color: $color-text;
      text-align: center;
      &.current {
        color: red;
      }
    }
  }
}
// 
.recommend-content {
  position: relative;
  z-index: 2;
  height: 100%;
  .bottom {
    padding-bottom: 1.47rem /* 55/37.5 */;
  }
}
// 
.swiper {
  overflow: hidden;
  position: relative;
  height: 12rem /* 450/37.5 */;
}
.swiper-img {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  transform-origin: center;
}
.commodity-text {
  background-color: #fff;
  padding: 0.32rem /* 12/37.5 */ 0.43rem /* 16/37.5 */;
  .title {
    font-size: 0.43rem /* 16/37.5 */;
    line-height: 1.4;
    color: $color-text-d;
  }
  .tags {
    overflow: hidden;
    padding-top: 0.27rem /* 10/37.5 */;
    .title-tag {
      display: inline-block;
      padding: 0 0.08rem /* 3/37.5 */;
      margin: 0 0.08rem /* 3/37.5 */ 0.08rem /* 3/37.5 */ 0;
      height: 0.43rem /* 16/37.5 */;
      line-height: 0.43rem /* 16/37.5 */;
      font-size: 0.27rem /* 10/37.5 */;
      color: #6d7d86;
      background-color: #eff3f6;
    }
  }
  .price-wrap {
    display: flex;
    align-items: center;
    padding-top: 0.27rem /* 10/37.5 */;
    .price {
      font-size: 0.59rem /* 22/37.5 */;
      color: $color-theme;
      .icon-renminbi {
        font-size: 0.32rem /* 12/37.5 */;
      }
    }
    .orgPrice {
      padding-left: 0.08rem /* 3/37.5 */;
      font-size: 0.32rem /* 12/37.5 */;
      color: $color-text-9;
    }
    .sale {
      font-size: 0.37rem /* 14/37.5 */;
      color: $color-text-9;
    }
  }
  .food-text {
    padding-top: 0.27rem /* 10/37.5 */;
    display: flex;
    align-items: center;
  }
  .freight {
    padding-right: 0.53rem /* 20/37.5 */;
    font-size: 0.32rem /* 12/37.5 */;
    color: $color-text-9;
  }
}
// 
.select {
  display: flex;
  box-sizing: content-box;
  // border-top: .27rem /* 10/37.5 */ solid #eee;
  padding: 0 0.43rem /* 16/37.5 */;
  height: 1.2rem /* 45/37.5 */;
  font-size: 0.37rem /* 14/37.5 */;
  color: $color-text-6;
  line-height: 1.2rem /* 45/37.5 */;
  background-color: #fff;
  .select-text {
    flex: 1;
  }
}
// 
.parameter {
  .bad {
    text-align: center;
    color: $color-theme;
  }
  .parameter-wrap {
    padding: 0 0.43rem /* 16/37.5 */;
    background-color: #fff;
  }
  .parameter-list {
    @include border-b-1px(0);
    line-height: 0.91rem /* 34/37.5 */;
    font-size: 0.37rem /* 14/37.5 */;
    &:last-child {
      &:after {
        border: none;
      }
    }
    .parameter-title {
      display: inline-block;
      width: 2.67rem /* 100/37.5 */;
      color: $color-text-9;
    }
    .parameter-desc {
      // #eb4868;
      color: $color-text;
    }
  }
}
// 
.particulars {
  padding-bottom: 0.13rem /* 10/75 */;
  background-color: #fff;
  font-size: 0;
  .particulars-img {
    margin-top: 0.21rem /* 8/37.5 */;
    width: 100%;
  }
}
// 
.comment-border {
  background-color: #fff;
}
// 
.comment-more {
  margin: 0.43rem /* 16/37.5 */ 0 0.53rem /* 20/37.5 */;
  text-align: center;
  .more-btn {
    font-size: 0.37rem /* 14/37.5 */;
    border: 1px solid $color-theme;
    color: $color-theme;
  }
}
// 
.footer-bar {
  position: fixed;
  bottom: -1px;
  z-index: 9;
  width: 100%;
  height: 1.36rem /* 51/37.5 */;
  background-color: #fff;
  .bar-wrap {
    @include border-b-1px(100%);
    display: flex;
    align-items: center;
    height: 100%;
    .bar-list {
      display: flex;
      flex-direction: column;
      justify-content: center;
      @include border-l-1px(100%);
      width: 1.33rem /* 50/37.5 */;
      height: 100%;
      text-align: center;
      .iconfont {
        font-size: 0.48rem /* 18/37.5 */;
        color: $color-text;
      }
      .bar-text {
        padding-top: 0.16rem /* 6/37.5 */;
        font-size: 0.32rem /* 12/37.5 */;
        color: $color-text-9;
      }
    }
    .bar-list-add {
      flex: 1;
      font-size: 0.43rem /* 16/37.5 */;
      color: #fff;
      line-height: 1.36rem /* 51/37.5 */;
      text-align: center;
      &.bar-yellow {
        background-color: #ff9800;
      }
      &.bar-red {
        background-color: #ff3355;
      }
    }
  }
}

// 
.hr-10 {
  width: 100%;
  height: 0.27rem /* 10/37.5 */;
  background: #eee;
}

// 
.list-detail-enter-active,
.list-detail-leave-active {
  transition: 0.3s all ease;
}
.list-detail-enter,
.list-detail-leave-to {
  transform: translate3d(100%, 0, 0);
}
</style>



